    &-items {
      justify-content: center; } }
  .facts__item {
    width: 100%; }
  .parameters__item {
    width: 100%;
    margin: 0 0 30px;
    &:last-child {
      margin: 0; } } }

@media (max-width: 590px) {
  .intro__preheader, .intro__subheader, .intro__text {
    font-size: 20px; }
  .intro__header {
    line-height: normal;
    font-size: 40px; }
  .btn {
