  .reviews-items .owl-nav button.owl-next {
    right: 10px; }
  .articles-items {
    padding: 0 10px; }
  .footer-social__header {
    width: 100%;
    margin: 0 0 10px;
    text-align: center; }
  .callback-modal__header {
    padding: 0 30px; } }
