        font-size: 18px;
        font-weight: 500;
        line-height: 18px;
        letter-spacing: 0.36px;
        &:hover {
          color: $green; } }
      & > ul {
        position: absolute;
        left: 0;
        top: calc(100%);
        width: 260px;
        padding-top: 12px;
        // padding: 12px 0
        box-shadow: 0 5px 10px -5px #000;
        border-radius: 10px;
        display: none;
        z-index: 10;
        & > li {
          background: #ffffff;
          &:first-child {
            padding-top: 12px;
            border-radius: 10px 10px 0 0; }
          &:last-child {
            padding-bottom: 15px;
            border-radius: 0 0 10px 10px; }
          & > a {
            font-weight: 500;
            display: block;
            padding: 5px 25px;
            color: #000;
            &:hover {
              color: $green; } } } } } }
  &.stickytop {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    background: #000;
    padding: 15px 0;
